import React from 'react'

const AddImage = props => {

    const img_pathUrl = (input) => {
        $('#img_url')[0].src = (window.URL ? URL : webkitURL).createObjectURL(input.files[0]);
    }

    let content = (
        <div>
            <div>
                <img className="h-16 w-16" src="" id="img_url" alt="your image"/>
                <input
                    accept="image/*,image/heif,image/heic,video/*,video/mp4,video/x-m4v,video/x-matroska"
                    multiple
                    type="file"
                    onChange={(event) => {
                        console.log(event.target.files[0])//event.files[0].mozFullPath);
                    }}
                />
                <div role="button" tabIndex="0">
                    <div>
                        <span>
                            <i role="img"></i>
                        </span>
                        <span dir="auto">
                            Photo/Video
                        </span>
                    </div>
                    <div data-visualcompletion="ignore">
                    </div>
                </div>
            </div>
        </div>
    )

    return content
}

export default AddImage